.App{
  background-color: #000;
}
.card-header,.card-footer,.card-body{
  background-color:#2E2E2E;
  color: #fff;
  font-family: Arial, Helvetica, sans-serif;
  font-weight: 600;
  font-style: italic;
}
@keyframes spin {
	0% {
		transform: rotateZ(0);
	}
	100% {
		transform: rotateZ(360deg);
	}
}
i {
	animation: spin 5s linear infinite;
}
.nav-head{
  font-weight: 600;
font-size: 45px;
}